
import nodata from '@/components/noData'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'

import { getOrderOgistics } from '@/api/user'
export default {
name: 'uc-orderLogistics',
components: {
Timeline,
TimelineItem,
nodata
},
data () {
return {
infoData: {}
}
},
created () {

const orderid = this.$route.query.id
const postData = {
orderid: orderid
}
getOrderOgistics(postData)
.then(res => {
if (res.data.errorcode === 0) {

this.infoData = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
}



